import { clsx, type ClassValue } from 'clsx';
import { format, parseISO, formatDistanceToNow } from 'date-fns';
import { ru } from 'date-fns/locale';

//     CSS
export function cn(...inputs: ClassValue[]) {
  return clsx(inputs);
}

//  
export function formatCurrency(amount: number): string {
  return new Intl.NumberFormat('ru-RU', {
    style: 'currency',
    currency: 'RUB',
    minimumFractionDigits: 0,
    maximumFractionDigits: 2,
  }).format(amount);
}

//  
export function formatNumber(value: number): string {
  return new Intl.NumberFormat('ru-RU').format(value);
}

//  
export function formatDate(date: string | Date): string {
  const dateObj = typeof date === 'string' ? parseISO(date) : date;
  return format(dateObj, 'dd.MM.yyyy', { locale: ru });
}

//    
export function formatDateTime(date: string | Date): string {
  const dateObj = typeof date === 'string' ? parseISO(date) : date;
  return format(dateObj, 'dd.MM.yyyy HH:mm', { locale: ru });
}

//  
export function formatRelativeTime(date: string | Date): string {
  const dateObj = typeof date === 'string' ? parseISO(date) : date;
  return formatDistanceToNow(dateObj, { addSuffix: true, locale: ru });
}

//    
export function getProductStatusColor(status: string): string {
  switch (status) {
    case 'active':
      return 'text-green-600 bg-green-100';
    case 'inactive':
      return 'text-yellow-600 bg-yellow-100';
    case 'discontinued':
      return 'text-red-600 bg-red-100';
    default:
      return 'text-gray-600 bg-gray-100';
  }
}

//    
export function getStockLevelColor(current: number, min: number, max?: number): string {
  if (current === 0) {
    return 'text-red-600 bg-red-100'; //   
  }
  if (current <= min) {
    return 'text-orange-600 bg-orange-100'; //  
  }
  if (max && current >= max) {
    return 'text-blue-600 bg-blue-100'; // 
  }
  return 'text-green-600 bg-green-100'; //  
}

//    
export function getProductStatusText(status: string): string {
  switch (status) {
    case 'active':
      return '';
    case 'inactive':
      return '';
    case 'discontinued':
      return '  ';
    default:
      return '';
  }
}

//    
export function getStockLevelText(current: number, min: number, max?: number): string {
  if (current === 0) {
    return '  ';
  }
  if (current <= min) {
    return ' ';
  }
  if (max && current >= max) {
    return '';
  }
  return ' ';
}

//  
export function debounce<T extends (...args: any[]) => any>(
  func: T,
  wait: number
): (...args: Parameters<T>) => void {
  let timeout: NodeJS.Timeout;
  return (...args: Parameters<T>) => {
    clearTimeout(timeout);
    timeout = setTimeout(() => func(...args), wait);
  };
}

//   ID
export function generateId(): string {
  return Math.random().toString(36).substr(2, 9);
}

//   email
export function isValidEmail(email: string): boolean {
  const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
  return emailRegex.test(email);
}

//   
export function isValidPhone(phone: string): boolean {
  const phoneRegex = /^[\+]?[1-9][\d]{0,15}$/;
  return phoneRegex.test(phone.replace(/\s/g, ''));
}

//  
export function truncateText(text: string, maxLength: number): string {
  if (text.length <= maxLength) return text;
  return text.substr(0, maxLength) + '...';
}

//   
export function capitalize(text: string): string {
  return text.charAt(0).toUpperCase() + text.slice(1);
}

//    
export function getInitials(name: string): string {
  return name
    .split(' ')
    .map(word => word.charAt(0))
    .join('')
    .toUpperCase()
    .slice(0, 2);
}

//  
export function downloadFile(blob: Blob, filename: string): void {
  const url = window.URL.createObjectURL(blob);
  const link = document.createElement('a');
  link.href = url;
  link.download = filename;
  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);
  window.URL.revokeObjectURL(url);
}

//    
export async function copyToClipboard(text: string): Promise<boolean> {
  try {
    await navigator.clipboard.writeText(text);
    return true;
  } catch (error) {
    // Fallback   
    const textArea = document.createElement('textarea');
    textArea.value = text;
    document.body.appendChild(textArea);
    textArea.select();
    const success = document.execCommand('copy');
    document.body.removeChild(textArea);
    return success;
  }
}

//    
export const browserSupport = {
  clipboard: !!navigator.clipboard,
  notifications: 'Notification' in window,
  serviceWorker: 'serviceWorker' in navigator,
  webShare: !!navigator.share,
}; 